<template>
  <div class="courseQualityQuestionnaire" v-loading.body="bodyLoading">
    <crumb style="padding: 0 5px;">
      <div class="new--btn" slot="right" @click="handleAdd" v-havaRole="'courseQualityQuestionnaireAdd'">+ 新建</div>
    </crumb>
    <div class="mainContainer">
      <query-filter :showData="queryAuthority" @onSelect="querySelect"></query-filter>

      <div class="tableBody">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%"
        >
          <el-table-column label="问卷名称" width="200"  :show-overflow-tooltip="true">
            <template slot-scope="scope">
              {{ scope.row.formName}}
            </template>
          </el-table-column>
          <el-table-column label="调查课程" width="200">
            <template slot-scope="scope">
              <span>{{ scope.row.ext3}}</span>
            </template>
          </el-table-column>
          <el-table-column label="问卷类型" prop="classifyName">
          </el-table-column>
          <!-- <el-table-column label="开始时间">
            <template slot-scope="scope">
              <span>{{ scope.row.startTime | time}}</span>
            </template>
          </el-table-column>
          <el-table-column label="结束时间">
            <template slot-scope="scope">
              <span>{{ scope.row.endTime | time}}</span>
            </template>
          </el-table-column> -->
          <el-table-column label="调查年级">
            <template slot-scope="scope">
              <span>{{ scope.row.ext2}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="380">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="handleEdit(scope.row)"
                v-havaRole="'courseQualityQuestionnaireEdit'"
              >
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;编辑问卷
              </el-button>
              <el-button
                type="text"
                size="small"
                style="color: #ff5858"
                @click="handleDelete(scope.row)"
                v-havaRole="'courseQualityQuestionnaireDel'"
              >
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;废弃
              </el-button>
              <el-button
                type="text"
                size="small"
                @click="handleView(scope.row)"
                v-havaRole="'courseQualityQuestionnaireView'"
              >
                <SvgIcon icon-class="iconyulan"></SvgIcon>&nbsp;预览
              </el-button>
              <el-button
                type="text"
                size="small"
                @click="handleStart(scope.row)"
                v-if="scope.row.id !=='null' && scope.row.status === 1"
                v-havaRole="'courseQualityQuestionnaireStart'"
              >
                <SvgIcon icon-class="iconfaqidiaocha"></SvgIcon>&nbsp;发起调查
              </el-button>
              <!-- <el-button
                type="text"
                size="small"
                @click="handleEntering(scope.row)"
                v-havaRole="'courseQualityQuestionnaireView'"
              >
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;问卷操作
              </el-button> -->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="form.page"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="form.pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryFilter from "../components/QueryFilter.vue";
import { formatDate } from "@/utils/utils";
import Crumb from '@/components/Crumb'

export default {
  components: {
    QueryFilter,
    Crumb
  },
  data() {
    return {
      form: {
        busiGroup3: "",
        ext1: "",
        ext2: "",
        ext3: "",
        page: 1,
        pageSize: 15
      },
      tableData: [],
      total: 0,
      bodyLoading: true,
      roleName: localStorage.getItem("roleName"),
      queryAuthority: [], //筛选条件
      api: {
        listUnSubmitCourseQuality:
          "/form/rest/FormMainBaseBusiness/listUnSubmitCourseQuality/v1",
        discard: "/form/rest/FormMainBaseBusiness/discard/v1",
        startInvestigation: "/form/rest/FormMainBaseBusiness/startCourseInvestigation/v1"
      }
    };
  },
  created() {
    if (this.roleName === "学院管理员" || this.roleName === "教师") {
      this.queryAuthority = [1, 2, 4, 5];
    } else if (this.roleName === "专业管理员") {
      this.queryAuthority = [2, 4, 5];
    } else if (this.roleName === "课程负责人") {
      this.queryAuthority = [2, 3];
    }
  },
  mounted() {
    this.getList();
  },
  methods: {
    querySelect(res) {
      this.form.busiGroup3 = res.specialtyId;
      this.form.ext1 = res.versionId;
      this.form.ext2 = res.classId;
      this.form.ext3 =
        this.roleName === "课程负责人" ? res.gradesId : res.schedulingGradesId;
      this.form.page = 1;
      this.form.pageSize = 15;
      this.getList();
    },
    getList() {
      this.$post(this.api.listUnSubmitCourseQuality, this.form).then(res => {
        this.bodyLoading = false;
        if (res.code === 200) {
          this.tableData = res.data.rows;
          this.total = res.data.total;
        }
      });
    },
     handleEntering(row){
       this.$router.push({
        path: "/evaluationAndFeedback/questionnaireOperation",
        query: {
          id:row.id
        }
      });
    },
    handleAdd() {
      this.$router.push({
        path: "/courseQualityEvaluation/NewQuestionnaire",
        query: {
          type: "course",
          status: "add"
        }
      });
    },
    handleEdit(row) {
      this.$router.push({
        path: "/courseQualityEvaluation/NewQuestionnaire",
        query: {
          type: "course",
          status: "edit",
          id: row.id
        }
      });
    },
    handleView(row) {
      this.$router.push({
        path: "/evaluationAndFeedback/questionnairePreview",
        query: {
          entryType: "rest",
          id: row.id
        }
      });
    },
    handleDelete(row) {
      this.$confirm("此操作将废弃该问卷, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        const postData = {
          id: row.id
        };
        this.$post(this.api.discard, postData).then(res => {
          if (res.code === 200) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: "操作成功"
            });
            this.getList();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.msg
            });
          }
        });
      });
    },
    handleStart(row) {
      this.$confirm(`确认发起${row.formName}的达成评价?`, "确认提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$post(this.api.startInvestigation, { formId: row.id }).then(
          res => {
            if (res.code === 200) {
                this.$notify.success({
                  title: "success",
                  duration: 1500,
                  message: "操作成功"
                });
            }
          }
        );
      });
    },
    handleSizeChange(val) {
      this.form.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.form.page = val;
      this.getList();
    }
  },
  filters: {
    time(val) {
      return val ? formatDate(val, "yyyy-MM-dd") : "";
    }
  }
};
</script>

<style lang="scss">
.courseQualityQuestionnaire {
  text-align: left;
  padding: 0 0 20px;
  .mainContainer {
    overflow: hidden;
    margin-bottom: 90px;
    .title {
      line-height: 40px;
      margin-bottom: 20px;
      span {
        font-size: 14px;
        font-weight: 700;
        color: #666;
      }
      .buttonGroup {
        margin-bottom: 10px;
        overflow: hidden;
        line-height: 32px;
        float: right;
      }
    }
    .listQuery {
      background-color: #fff;
      border-radius: 10px;
      margin: 0 5px 20px;
    }
    .tableBody {
      overflow: initial;
      border-radius: 10px 10px 0 0;
      margin: 0 5px;
      .el-table {
        .distance {
          margin-right: 10px;
        }
        .el-table__header {
          tr {
            height: 50px;
            th {
              background: #fff;
              .cell {
                color: #666;
                font-weight: 700;
              }
            }
          }
        }
        .el-table__body {
          .el-table__row {
            height: 40px;
            td {
              padding: 0;
            }
          }
        }
      }
    }
    .pagination {
      padding: 20px;
      margin: 0 5px;
      text-align: right;
      background-color: #fff;
      border-radius: 0 0 10px 10px;
    }
  }
}
</style>



